:root {
    --navigation-color: #202020;
    --navigation-color-transparent: rgba(15, 14, 14, 0.7);
    --navigation-color-secondary: #e07f7f;
    --navigation-color-tertiary: rgb(15, 15, 15);
    --text-color: #e2e2e2;
    --contact-color: #e26f2e;
    --contact-color-transparent: rgba(226, 111, 46, 0.7);
    --about-color: #3f48cc;
    --about-color-transparent: rgb(63, 72, 204, 0.7);
    --projects-color: #ea8aba;
    --projects-color-transparent: rgba(234, 138, 186, 0.7);
    --main-color: #193225;
    --main-color-transparent: rgba(25, 50, 37, 0.7);
}

* {
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    padding: 0;
    background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
        url("Images/background/nature.jpg");
    background-size: 100% 100%;
}

.react-root,
html,
body {
    height: 100%;
    overflow-x: hidden;
}

.glass-box {
    padding: 20px 40px;
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border: 1px solid rgb(255, 255, 255, 0.1);
    border-radius: 20px;
    backdrop-filter: blur(15px);
}

.glass-box-container {
    height: 96%;
    width: 98%;
    margin: 1%;
    padding: 0;
}

.white-text {
    color: var(--text-color);
    font-size: larger;
}

.transparent-button {
    font-weight: bold;
    background-color: transparent;
    white-space: nowrap;
    border: 0;
    cursor: pointer;
}

/** Navigation Bar */
.navigation-bar {
    position: relative;
    background-color: var(--navigation-color-transparent);
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
    height: 6vh;
    width: 100%;
    padding-bottom: calc(20px + 1vh);
    z-index: 20;
    border: none;
    padding: 0;
    padding-bottom: 1vh;
    display: flex;
    align-items: stretch;
}

.navigation-logo {
    flex: 1;
}

.navigation-ul {
    width: 70%;
    display: flex;
    justify-content: space-around;
}

.navigation-ul li {
    list-style: none;
    width: 20%;
    height: 100%;
}

.fill-container {
    width: 100%;
    height: 100%;
}

.navigation-container {
    height: 100%;
    letter-spacing: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigation-container:hover button {
    color: var(--navigation-color-secondary);
}

.burger {
    display: none;
}

.burger div {
    width: 25px;
    height: 3px;
    background-color: var(--text-color);
    margin: 3px;
    transition: all 0.3s ease;
}

.burgerToggle .line1 {
    transform: rotate(-45deg) translate(-6.25px, 6.25px);
}

.burgerToggle .line2 {
    opacity: 0;
}

.burgerToggle .line3 {
    transform: rotate(45deg) translate(-6.25px, -6.25px);
}

.login-button {
    width: 60%;
    height: 60%;
}

/** Page bar */
.page-bar {
    position: fixed;
    top: 5vh;
    width: 100%;
    height: 1vh;
    z-index: 21;
    background-color: var(--about-color);
}

/** Footer bar */
.bottom-bar {
    position: absolute;
    height: 6vh;
    width: 100%;
    background-color: var(--navigation-color-transparent);
    justify-content: center;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bottom-bar ul {
    display: flex;
    justify-content: space-around;
    width: 20%;
    margin-left: 10%;
}

.bottom-bar ul li {
    list-style: none;
}

.bottom-bar ul li a {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-links:hover {
    transform: scale(0.9);
    filter: invert(1);
    border-radius: 50px;
}

.footer-links {
    width: 50%;
}

/** Everything needed for the RGB button*/
.rgb-button {
    position: relative;
    background: var(--navigation-color-transparent);
    border-radius: 50px;
    padding: 1px;
}

.rgb-button span {
    position: relative;
    display: inline-block;
    background-color: var(--navigation-color-tertiary);
    z-index: 1;
    height: 100%;
    width: 100%;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.rgb-button::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        45deg,
        #fb0094,
        #00f,
        #0f0,
        #ff0,
        #f00,
        #fb0094,
        #00f,
        #0f0,
        #ff0,
        #f00
    );
    opacity: 0;
    background-size: 400%;
    transition: 0.5s;
    filter: blur(4px);
    animation: animate_button 20s linear infinite;
    border-radius: 50px;
}

.rgb-button:hover::after {
    opacity: 1;
}

@keyframes animate_button {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 300% 0;
    }

    100% {
        background-position: 0 0;
    }
}

/** The full container*/
.container {
    width: 100%;
    height: 84vh;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: justify;
    padding: 20px;
    padding-left: 10%;
    padding-right: 10%;
}

/** Index page */
.index {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.index h1 {
    font-size: 10vh;
}

.index h2 {
    font-size: 3vh;
}

/* Other Screen Sizes */
@media screen and (max-width: 768px) {
    .navigation-bar {
        height: 5vh;
        padding-bottom: 0;
    }

    .container {
        height: 85vh;
    }

    .navigation-ul {
        position: absolute;
        right: 0;
        height: 85vh;
        width: 0;
        top: 5vh;
        background-color: var(--navigation-color-transparent);
        flex-direction: column-reverse;
        transform: translateX(100%);
        transition: transform 0.3s ease-in;
        box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
        backdrop-filter: blur(15px);
    }

    .navigation-ul li {
        width: 100%;
        height: 19%;
    }

    .burger {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 20%;
        height: 100%;
    }

    .page-bar {
        right: 0px;
        width: 0px;
        height: 85vh;
        transform: translateX(99%);
        transition: 0.3s;
        box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
        z-index: 10;
        backdrop-filter: blur(15px);
    }
}

/** All actives */
.nav-active-bar {
    width: 61%;
    transform: translateX(0%);
}

.nav-active {
    width: 60%;
    transform: translateX(0%);
}
